@layer base {
  :root {
    color-scheme: dark;
    --background: #020617;
    --foreground: #e2e8f0;
    --app-text-scale: 1;
    --sidebar-width: 280px;
  }
  html,
  body {
    height: 100%;
    overflow: hidden;
    word-break: break-word;
    font-size: calc(1rem * var(--app-text-scale, 1));
    background: #020617;
  }
  body {
    background: radial-gradient(ellipse at 15% 10%, rgba(34, 211, 238, 0.07), transparent 40%),
      radial-gradient(ellipse at 85% 5%, rgba(94, 234, 212, 0.06), transparent 35%),
      radial-gradient(ellipse at 50% 90%, rgba(99, 102, 241, 0.04), transparent 50%),
      #020617;
    color: var(--foreground);
    font-family: var(--font-geist-sans), system-ui, -apple-system, sans-serif;
  }
  * {
    scrollbar-width: thin;
    scrollbar-color: rgba(100, 116, 139, 0.35) transparent;
  }
  *::-webkit-scrollbar {
    width: 5px;
    height: 5px;
  }
  *::-webkit-scrollbar-track {
    background: transparent;
  }
  *::-webkit-scrollbar-thumb {
    background: rgba(100, 116, 139, 0.35);
    border-radius: 10px;
    transition: background 0.2s ease;
  }
  *::-webkit-scrollbar-thumb:hover {
    background: rgba(100, 116, 139, 0.6);
  }
  .scrollbar-thin::-webkit-scrollbar {
    width: 4px;
  }
  .scrollbar-thin::-webkit-scrollbar-track {
    background: transparent;
  }
  .scrollbar-thin::-webkit-scrollbar-thumb {
    background: rgba(100, 116, 139, 0.3);
    border-radius: 6px;
  }
  .scrollbar-thin::-webkit-scrollbar-thumb:hover {
    background: rgba(100, 116, 139, 0.55);
  }
  button,
  a,
  select,
  input,
  textarea {
    transition-property: color, background-color, border-color, box-shadow, opacity, transform;
    transition-duration: 150ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  }
  .sidebar-item {
    position: relative;
    transition: all 150ms ease;
  }
  .sidebar-item::before {
    content: "";
    position: absolute;
    inset: 0;
    border-radius: 0.5rem;
    opacity: 0;
    background: linear-gradient(135deg, rgba(34, 211, 238, 0.06), transparent);
    transition: opacity 200ms ease;
    pointer-events: none;
  }
  .sidebar-item:hover::before {
    opacity: 1;
  }
  @keyframes message-in {
    from {
      opacity: 0;
      transform: translateY(6px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  .animate-message-in {
    animation: message-in 0.25s ease-out both;
  }
  @keyframes search-progress {
    0% {
      width: 0%;
      margin-left: 0%;
    }
    50% {
      width: 40%;
      margin-left: 30%;
    }
    100% {
      width: 0%;
      margin-left: 100%;
    }
  }
  .animate-search-progress {
    animation: search-progress 1.5s ease-in-out infinite;
  }
  @keyframes pulse-glow {
    0%, 100% {
      box-shadow: 0 0 0 0 rgba(16, 185, 129, 0.4);
    }
    50% {
      box-shadow: 0 0 0 4px rgba(16, 185, 129, 0);
    }
  }
  .animate-pulse-glow {
    animation: pulse-glow 2.5s ease-in-out infinite;
  }
  @keyframes dot-bounce {
    0%, 80%, 100% {
      transform: translateY(0);
      opacity: 0.4;
    }
